.b3dviewer-wrapper {
  max-width: 100%;
  width: 100%;
  height: 100%;
  min-width: 250px;
}
.pagination .b3dviewer-wrapper {
  // height: calc(100% - 70px);
}

.b3dviewer.aligncenter {
  text-align: left;
}

.b3dviewer-wrapper model-viewer {
  width: 100%;
  height: 100%;
  position: relative;
}

.b3dviewer-wrapper .slider {
  width: fit-content;
  text-align: center;
  overflow: hidden;
  position: absolute;
  bottom: 16px;
}
.b3dviewer-wrapper * {
  box-sizing: border-box;
}
.b3dviewer-wrapper .slides {
  display: flex;
  overflow-x: auto;
  scroll-snap-type: x mandatory;
  scroll-behavior: smooth;
  -webkit-overflow-scrolling: touch;
  margin-left: 10px;
  overflow-y: hidden;
}
.slides .slide.selected,
.modelThumbs .selected {
  border: 2px solid #b3b3b3;
}
.slideController {
  .arrow-left,
  .arrow-right {
    cursor: pointer;
    &.disabled {
      cursor: not-allowed;
      opacity: 0.3;
    }
  }
  position: absolute;
  display: flex;
  justify-content: space-between;
  align-items: center;
  top: 50%;
  width: 100%;
}

.slides {
  .slide {
    scroll-snap-align: start;
    flex-shrink: 0;
    width: 90px;
    height: 90px;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    background-color: #fff;
    margin-right: 10px;
    border: none;
    display: flex;
    padding: 5px;
    border-radius: 5px;
    cursor: pointer;
    @media (max-width: 576px) {
      width: 60px;
      height: 60px;
    }
  }
  .slide-number {
    background: #ebebeb;
    text-align: center;
    span {
      font-size: 65px;
      text-align: center;
      margin: auto;
      color: #666;
    }
  }
}
.b3dviewer-wrapper .slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 5px;
}
.woocommerce div.product div.images img {
  height: 100%;
  object-fit: cover;
}
.modelViewerBlock .woocommerce .bp_model_parent.b3dviewer-wrapper img {
  height: 100%;
  object-fit: cover;
}
.bp_model_parent {
  .DMC canvas {
    pointer-events: none;
  }
  &:fullscreen {
    height: 100vh;
  }
  &:fullscreen {
    .online_3d_viewer {
      height: 100% !important;
    }
  }
}

.online_3d_viewer {
  max-width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  div:last-child {
    text-align: center;
    vertical-align: center;
  }
}

#openBtn {
  position: absolute;
  right: 10px;
  bottom: 10px;
  z-index: 999999;
  fill: #333;
  cursor: pointer;
  background: #fff;
  padding: 5px;
  border-radius: 2px;
  box-sizing: border-box;
}

#closeBtn {
  position: absolute;
  top: 10px;
  right: 20px;
  z-index: 999999;
  fill: #333;
  cursor: pointer;
  display: none;
  background: #fff;
  border-radius: 2px;
}
.b3dviewer .fullscreen #closeBtn {
  display: block;
}
.modelViewerBlock,
.modelViewerBlock.b3dviewer {
  max-width: 100%;
  &.alignright,
  &.alignend {
    margin-left: auto;
  }
  &.aligncenter {
    margin-left: auto;
    margin-right: auto;
  }
  &.alignleft,
  &.alignstart {
    margin-right: auto;
  }
  .fullscreen {
    width: 100vw !important;
    height: 100vh !important;
    position: fixed;
    z-index: 99999;
    background: #fff;
    animation-name: fullscreen;
    animation-duration: 0.5s;
    top: 0;
    left: 0;
  }
  #openBtn {
    display: none;
    @media screen and (max-width: 768px) {
      display: block;
    }
  }
  .bp_model_parent:fullscreen {
    // height: 100vh;
    #openBtn {
      display: none;
    }
    #closeBtn {
      display: block;
    }
  }

  #default-poster {
    width: 100%;
    height: 100%;
    background-size: cover;
    background-color: transparent;
    border: none;
    outline: none;
  }

  .modelThumbs {
    display: flex;
    gap: 10px;
    height: 70px;
    max-width: 100%;
    flex-wrap: wrap;
    margin-top: 10px;
    .thumbsItem {
      width: 100px;
      height: 80px;
      cursor: pointer;
    }
  }
}
.b3dviewer-wrapper .select {
  background: white;
  display: inline-block;
  padding: 5px 12px;
  margin-left: 5px;
  margin-top: 5px;
  display: none;
  border-radius: 3px;
}
.b3dviewer-wrapper .select select {
  padding: 0 24px 0 8px;
  display: inline-block;
  width: auto;
}
.percentageWrapper .overlay {
  width: 100%;
  height: 100%;
  background: #333333a1;
  position: absolute;
  top: 0;
  left: 0;
}
.percentageWrapper .percentage {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #fff;
}
.b3dviewer .fullscreen model-viewer {
  height: 100% !important;
}
model-viewer.hide_progressbar::part(default-progress-bar) {
  display: none;
}

.woocommerce-product-gallery.images .b3dviewer.woocommerce {
  width: 100%;
}
.woocommerce-page .b3dviewer_woo_wrapper .woocommerce-product-gallery.images {
  width: 100%;
}
.woocommerce-page .b3dviewer_woo_wrapper .b3dviewer.woocommerce,
.woocommerce #content div.product div.images,
.woocommerce-page #content .ast-woocommerce-container div.product .b3dviewer_woo_wrapper div.images {
  width: 100%;
}

.b3dviewer_woo_wrapper {
  float: left;
  width: 48%;
  display: flex;
  flex-direction: column;
}
.woocommerce-page .product .b3dviewer.woocommerce:not(.b3dviewer_woo_wrapper .b3dviewer.woocommerce) {
  float: left;
  width: 48%;
}

.woocommerce-page .product .b3dviewer.woocommerce,
.b3dviewer_woo_wrapper {
}
#animations {
  font-size: 16px;
}
.fullscreen {
  .online_3d_viewer,
  canvas {
    width: 100% !important;
    height: 100% !important;
    // canvas {
    //   margin: 0 auto;
    // }
  }
}

.woocommerce-product-gallery.woocommerce-product-gallery--with-images {
  display: flex;
  flex-direction: column;
}
.modelViewerBlock #default-poster {
  pointer-events: none;
}

@media only screen and (min-width: 768px) {
  .woocommerce #content .shoptimizer-archive div.product div.images {
    width: 60%;
  }
}
